import React, { Component } from 'react';

const loader = 'data:image/gif;base64,';

class Loader extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div id="loader">
                <img id="loading-image" src={loader} />
            </div>
        );
    }
}

export default Loader;
